@import "~antd/dist/antd.css";

body {
  margin: 0;
  font-family: "IBM Plex Sans", sans-serif;
}

.main {
  width: 100%;
  height: 100vh;
}

.logo-container {
  align-items: center;
  display: flex;
  justify-content: center;
  margin-top: 20px;
  max-width: 200px;
}

.logo-container img {
  transition: width 0.5;
}

.logo-large {
  width: 130px;
}

.logo-small {
  width: 40px;
  height: 40px;
}

.site-layout-sub-header-background {
  background: #fff;
}

.site-layout-background {
  background: #fff;
}

.ant-radio-group label {
  margin: 5px;
}

.pointer {
  cursor: pointer;
}

.d-flex-row {
  display: flex;
  flex-direction: row;
}

.d-flex-col {
  display: flex;
  flex-direction: column;
}

.margin-zero {
  margin: 0;
}

.ant-checkbox-wrapper {
  margin: 0 !important;
  padding: 5px;
  border-radius: 5px;
}
.ant-checkbox-wrapper:hover {
  background-color: #e6fffb;
}

.ant-tag:focus {
  color: #ffffff;
  background-color: rgb(0, 0, 0);
  border-color: rgb(0, 0, 0);
}

.ant-btn-primary {
  background-color: rgb(0, 0, 0);
  border-color: rgb(0, 0, 0);
}

.ant-btn-primary:hover {
  background-color: rgb(0, 0, 0);
  border-color: rgb(0, 0, 0);
}

.ant-btn-primary:focus {
  background-color: rgb(0, 0, 0);
  border-color: rgb(0, 0, 0);
}

.ant-table-cell {
  word-wrap: break-word;
}

.ant-image-img {
  padding: 20px !important;
}

.grid-responsive {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  padding: 10px;
  grid-gap: 10px;
}

@media (min-width: 768px) {
  .grid-responsive {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 1000px) {
  .grid-responsive {
    grid-template-columns: repeat(4, 1fr);
  }
}
@media (min-width: 1200px) {
  .grid-responsive {
    grid-template-columns: repeat(5, 1fr);
  }
}
@media (min-width: 1400px) {
  .grid-responsive {
    grid-template-columns: repeat(6, 1fr);
  }
}

*::-webkit-scrollbar {
  width: 10px; /* width of the entire scrollbar */
}

*::-webkit-scrollbar-track {
  background: rgb(216, 215, 214); /* color of the tracking area */
}

*::-webkit-scrollbar-thumb {
  background-color: rgb(184, 184, 187); /* color of the scroll thumb */
  border-radius: 20px; /* roundness of the scroll thumb */
  border: 3px solid rgb(184, 184, 187); /* creates padding around scroll thumb */
}

.on-hover:hover {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: box-shadow 0.6s;
}
